import { mapGetters } from 'vuex';
export default {
  name: 'ToggleDark',
  data() {
    return {
      isDark: this.$store.state.base.useDarkMode || document.documentElement.classList.contains('dark')
    };
  },
  methods: {
    handleToggleClick() {
      this.isDark = !this.isDark;
      this.$store.commit('base/setUseDarkMode', this.isDark);
    }
  }
};